}
main .left .logo {
  min-width: 225px;
  width: 100%;
  background: url("../../public/assets/suggestions/desktop/background-header.png");
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  min-height: 137px;
  border-radius: 10px;
  display: flex;
  justify-content: end;
  flex-direction: column;
  padding: 1.2rem 1.75rem;
}
